"use client";
import React from "react";
import { Button } from "@/components/ui/button";
import { LogOut } from "lucide-react";
import LogoutModal from "./logout";
import { useLogoutModal } from "./useLogoutModal";

interface LogoutTriggerProps {
  variant?:
    | "default"
    | "destructive"
    | "outline"
    | "secondary"
    | "ghost"
    | "link";
  size?: "default" | "sm" | "lg" | "icon";
  className?: string;
  children?: React.ReactNode;
}

export const LogoutTrigger: React.FC<LogoutTriggerProps> = ({
  variant = "outline",
  size = "default",
  className = "",
  children,
}) => {
  const { isOpen, openModal, closeModal } = useLogoutModal();

  return (
    <>
      <Button
        variant={variant}
        size={size}
        className={className}
        onClick={openModal}
      >
        {children || (
          <>
            <LogOut size={16} className="mr-2" />
            Logout
          </>
        )}
      </Button>

      <LogoutModal isOpen={isOpen} onClose={closeModal} />
    </>
  );
};
