"use client";
import React from "react";
import { useRouter } from "next/navigation";
import { useSnackbar } from "notistack";
import { useAuth } from "@/components/auth";
import { Button } from "@/components/ui/button";
import { LogOut } from "lucide-react";
import { LogoutTrigger } from "@/components/modals/logout-trigger";

interface LogoutButtonProps {
  variant?:
    | "default"
    | "destructive"
    | "outline"
    | "secondary"
    | "ghost"
    | "link";
  size?: "default" | "sm" | "lg" | "icon";
  className?: string;
  children?: React.ReactNode;
  showConfirmation?: boolean;
  useModal?: boolean;
}

export const LogoutButton: React.FC<LogoutButtonProps> = ({
  variant = "outline",
  size = "default",
  className = "",
  children = "Logout",
  showConfirmation = false,
  useModal = true, // Default to using modal
}) => {
  // If useModal is true, use the modal trigger
  if (useModal) {
    return (
      <LogoutTrigger variant={variant} size={size} className={className}>
        {children}
      </LogoutTrigger>
    );
  }

  // Original immediate logout functionality (kept for backward compatibility)
  const router = useRouter();
  const { enqueueSnackbar } = useSnackbar();
  const { logout } = useAuth();

  const handleLogout = () => {
    if (showConfirmation) {
      if (!window.confirm("Are you sure you want to logout?")) {
        return;
      }
    }

    try {
      enqueueSnackbar("Logged out successfully", { variant: "success" });

      // Clear auth data and redirect with refresh
      setTimeout(() => {
        logout();
        router.replace("/login");
        // Force page refresh to reset theme/state
        setTimeout(() => {
          window.location.reload();
        }, 100);
      }, 200);
    } catch (error) {
      console.error("Logout error:", error);
      enqueueSnackbar("Error during logout", { variant: "error" });
    }
  };

  return (
    <Button
      variant={variant}
      size={size}
      className={className}
      onClick={handleLogout}
    >
      {children}
    </Button>
  );
};
